import React from "react"

const DownIcon = (props) => {
    return(
        <div
        style={{
          display: "flex",
          flexDirection:"row",
          alignItems:"center",
          justifyContent:"center",
          marginBottom:"-34px",
          transform:"translateY(-45px)"
        }}
        >
        <div
        style={{
          maxWidth:"30px"
        }}
        >
        <img src={props.icon} id="downicon" alt="down" />
        </div>
        </div>
    )
}
export default DownIcon


